<svelte:head>
	<link href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css" rel="stylesheet" integrity="sha384-mhpbKVUOPCSocLzx2ElRISIORFRwr1ZbO9bAlowgM5kO7hnpRBe+brVj8NNPUiFs" crossorigin="anonymous">
<script src="https://www.gstatic.com/firebasejs/7.14.6/firebase-remote-config.js"></script>
</svelte:head>
<script context="module">
	
  import {  Button, Col, Container, Row, Form, FormGroup, FormText, Input, CustomInput, Label } from 'sveltestrap';

let yourData,ideasArray, randomIdea="";

export function marketingIdeas() {
		// Trying to remotely get and update ideas
		fetch("https://api.apispreadsheets.com/data/626/").then(res=>{
			if (res.status === 200){
				// SUCCESS
				res.json().then(data=>{
					yourData = data.data
					ideasArray = Object.values(yourData)

					randomIdea = ideasArray[Math.round(Math.random() * ideasArray.length)].Idea;
					//console.log(randomIdea);
					
				}).catch(err => console.log(err))
			}
			else{
				// ERROR
			}
		})

		return randomIdea;
	}
</script>

